<template>
  <div class="sidebar">
    <!--router，使得el-menu-item的index可以作为索引，
    再通过索引去router中查找-->
    <el-menu
      class="sidebar-e1-menu"
      :collapse="collapse"
      background-color="#334256"
      text-color="#fff"
      active-text-color="#20a0ff"
      router
    >
      <template v-for="item in items">
        <template>
          <el-menu-item :index="item.index" :key="item.index">
            <i :class="item.icon"></i>
            <div style="font-size: 16px;display: inline-block"> {{item.title}}</div>
          </el-menu-item>
        </template>
      </template>
    </el-menu>
  </div>
</template>

<script>

  export default {
    data(){
      return{
        collapse: false,

        items: [
          {
            icon: 'el-icon-document',
            index: 'main',
            title: '系统首页'
          },
          {
            icon: 'el-icon-document',
            index: 'single',
            title: '单个企业分类'
          },
          {
            icon: 'el-icon-document',
            index: 'multiple',
            title: '批量企业分类'
          },
        ]
      }
    },
    computed: {

    },
    created() {

    }
  }
</script>

<style scoped>
  .sidebar {
    display: block;
    position: absolute;
    left: 0;
    top: 70px;
    bottom: 0;
    background-color: #334256;
    overflow-y: scroll;
    height: 700px;
  }

  .sidebar::-webkit-scrollbar{
    width: 0;
  }

  /*collapse为false时生效*/
  .sidebar-e1-menu:not(.el-menu--collapse) {
    width: 185px;
  }

  .sidebar >ul {
    height: 100%;
  }

</style>



